import React, { useEffect, useState } from 'react'
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemIcon from "@mui/material/ListItemIcon";
import Button from "@mui/material/Button";
import useWindowDimensions from "../helpers";
import { Link } from "react-router-dom";
import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import withStyles from "@mui/styles/withStyles";
import ErrorBoundary from "../ErrorBoundary";
import { isMobile } from 'react-device-detect';

const LandingPage = () => {

    const [y, setY] = useState(null);

    const box = React.createRef();

    useEffect(() => {
        if (!box || isMobile) return;

        const pos = box.current?.offsetTop;
        const height = box.current?.offsetHeight;


        if (y > pos && y < pos + height) {
            const sliderPos = Math.round((y - pos) / height * 100);
            localStorage.setItem('height', String(sliderPos));
        }

    }, [y]);

    const { width } = useWindowDimensions();

    const checkWidth = width > 800;
    const checkWidth2 = width < 2250;

    const handleHeight = (value) => {
        if (localStorage.getItem('height') !== String(100 - value)) {
            localStorage.setItem('height', String(100 - value));
        }
    };

    const CustomSlider = withStyles({
        root: {
            color: "#679",
            width: 7
        },
        disabled: {
            color: "#679"
        },
        thumb: {
            height: 30,
            width: 30,
        },
        rail: {
            color: "#679"
        },
        track: {
            color: "#679"
        }
    })(Slider);

    return (
        <>
            {!isMobile &&
                <div style={{
                    position: "fixed",
                    padding: 0,
                    margin: 0,
                    top: 0,
                    left: 0,
                    width: "100%",
                    height: "100%"
                }}
                    onMouseMove={event => setY(event.pageY)}></div>
            }
            <Grid container spacing={2} align="center" sx={{ height: "100vh", paddingX: checkWidth2 ? 0 : 50 }}>
                {checkWidth &&
                    <Grid item xs={checkWidth2 ? 6 : 8} justify="center" sx={{ marginY: "auto" }}>
                        <Grid container>
                            <Grid item xs={11}>
                                <iframe id="iframe1" title="visualizer" src="/visualizer/LandingPageVis.html"
                                    style={{ height: "50vh" }} />
                            </Grid>
                            <Grid item xs={1}>
                                <Box sx={{ height: "100%" }} ref={box}>
                                    <ErrorBoundary>
                                        <CustomSlider
                                            sx={{
                                                '& input[type="range"]': {
                                                    WebkitAppearance: 'slider-vertical',
                                                },
                                            }}
                                            orientation="vertical"
                                            defaultValue={localStorage.getItem('height') === null ? 30 : 100 - localStorage.getItem('height')}
                                            aria-label="Height"
                                            getAriaValueText={handleHeight}
                                            valueLabelDisplay="off"
                                            disabled={!isMobile}
                                        />
                                    </ErrorBoundary>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                }
                <Grid item xs={checkWidth ? checkWidth2 ? 6 : 4 : 12} justify="center" sx={{ marginY: "auto" }}>
                    <Container sx={{ maxWidth: "650px!important" }}>
                        <Typography variant="h2" component="div">Microphone and Music Visualizer</Typography>
                        <List sx={{ marginY: 5 }} onMouseMove={event => setY(event.pageY)}>
                            <ListItem sx={{ marginY: -1 }}>
                                <ListItemIcon>
                                    <Typography component="p">⬤</Typography>
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant="h4" sx={{ fontWeight: 700 }}> Free </Typography>
                                </ListItemText>
                            </ListItem>
                            <ListItem sx={{ marginY: -3 }}>
                                <ListItemIcon>
                                    <Typography component="p">⬤</Typography>
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant="h4" sx={{ fontWeight: 700 }}> No&nbsp;Login</Typography>
                                </ListItemText>
                            </ListItem>
                            <ListItem sx={{ marginY: -3 }}>
                                <ListItemIcon>
                                    <Typography component="p">⬤</Typography>
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant="h4" sx={{ fontWeight: 700 }}> Forever </Typography>
                                </ListItemText>
                            </ListItem>
                        </List>
                    </Container>
                    <Link to="./visualiser" style={{ textDecoration: "none" }}>
                        <Button variant="contained" size="large" onClick={() => localStorage.setItem('height', 90)}
                            onMouseMove={event => !isMobile && setY(event.pageY)}
                            sx={{ marginY: 5, paddingX: 15, paddingY: 1.5, borderRadius: 15 }}>
                            Open Visualizer
                        </Button>
                    </Link>
                </Grid>
            </Grid>
        </>
    )
};

export default LandingPage;