import React from 'react';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Box from "@mui/material/Box";

const LineCircleShow = (classes) => {
    const [checked, setChecked] = React.useState(localStorage.getItem('showLineCircle') === null ? false : 'true' === localStorage.getItem('showLineCircle'));
    const vis = localStorage.getItem('vis') === null ? 'bars' : localStorage.getItem('vis');

    const handleChange = (event) => {
        setChecked(event.target.checked);
        localStorage.setItem('showLineCircle', event.target.checked)
    };

    if (vis === 'line_circle' || vis === 'colorful_circle') {
        return (
            <FormControl className={"all-form-inputs " + classes.formControl}>
                <Box
                    component="form"
                    sx={{
                        '& .MuiTextField-root': { m: 1, width: '25ch' },
                    }}
                    noValidate
                    autoComplete="off"
                >
                    <FormControlLabel className={classes.formControl} style={{ color: "#b9b9bb" }}
                        control={
                            <Checkbox
                                checked={checked}
                                onChange={handleChange}
                                name="checkedB"
                                color="primary"
                            />
                        }
                        label="Show Circle"
                    />
                </Box>
            </FormControl>
        );
    } else {
        return (<></>);
    }
};


export default LineCircleShow;