import React from 'react';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import Slider from '@mui/material/Slider';
import Box from "@mui/material/Box";


const Datapoints = (classes) => {


    const handleBarMultiple = (value) => {
        if (localStorage.getItem('barMultiple') != value) {
            localStorage.setItem('barMultiple', value);
        }
    }

    const marks = [
        {
            value: 5,
            label: '2^5',
        },
        {
            value: 8,
            label: '2^8',
        },
        {
            value: 12,
            label: '2^12',
        },
    ];

    return (
        <FormControl className={"all-form-inputs " + classes.formControl}>
            <Box
                component="form"
                sx={{
                    '& .MuiTextField-root': { m: 1, width: '25ch' },
                }}
                noValidate
                autoComplete="off"
            >
                <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                    Amount of Datapoints
                </InputLabel>
                <Slider style={{ marginTop: "24px" }}
                    getAriaValueText={handleBarMultiple}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    step={1}
                    marks={marks}
                    min={5}
                    max={14}
                    defaultValue={localStorage.getItem('barMultiple') === null ? 9 : localStorage.getItem('barMultiple')}
                />
            </Box>
        </FormControl>
    );
};


export default Datapoints;