import React from 'react'
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import ColorPicker from 'material-ui-color-picker'
import Box from "@mui/material/Box";
import makeStyles from "@mui/styles/makeStyles/makeStyles";


const FillColor = (classes) => {

    let color = (localStorage.getItem('fillcolor') === null ? '#ffffff' : localStorage.getItem('fillcolor'));
    const vis = localStorage.getItem('vis') === null ? 'bars' : localStorage.getItem('vis');

    const aSettings = localStorage.getItem('settings') === "Advanced";

    const setColor = (color) => {
        if (color !== undefined && localStorage.getItem('fillcolor') !== color) {
            localStorage.setItem('fillcolor', color);

            if(!aSettings){
                localStorage.setItem('linecolor', color);
            }
        }
    };

    const useStyles = makeStyles({
        root: {
            '& .MuiInput-underline:before': {
                borderBottomColor: '#fff8 !important', // Semi-transparent underline
            },
            '& .MuiInput-underline:after': {
                borderBottomColor: '#90caf9 !important', // Solid underline on hover
            },
        },
    });
    classes = useStyles();

    if (vis === 'multiColor' || vis === 'colorful_circle') {
        return (<></>);
    } else {
        return (
            <FormControl className={classes.formControl}>
                <Box
                    component="form"
                    sx={{
                        '& .MuiTextField-root': { m: 1, minWidth: '25ch' },
                    }}
                    noValidate
                    autoComplete="off"
                >
                    <InputLabel className={"all-form-inputs"} shrink id="demo-simple-select-placeholder-label-label">
                        Fill Color
                    </InputLabel>
                    <ColorPicker style={{ marginTop: "18px" }}
                        name='color'
                        defaultValue={color}
                        className={"all-form-inputs " + classes.root}
                        // value={this.state.color} - for controlled component

                        onChange={setColor}

                    />
                </Box>
            </FormControl>
        )
    }
};

export default FillColor;