import { Button } from "@mui/material";
import { useState } from "react";
import { createSettings } from "../helpers";


const TopSelection = () => {

    const [text, setText] = useState("Save Settings as Link");
    const [sText, setSText] = useState(localStorage.getItem('settings') === "Advanced" ? "Simple" : "Advanced");

    const saveToClipboard = () => {
        navigator.clipboard.writeText("http://localhost:3000/#/visualiser?settings=" + createSettings()).then(() => {
            setText("Copied!");
            setTimeout(() => { setText("Save Settings as Link") }, 1500);
        })
    }

    const changeSettingsType = () => {
        if (sText === "Advanced") {
            setSText("Simple");
            localStorage.setItem('settings', 'Advanced');
            global.refreshCanvas();
        } else {
            setSText("Advanced");
            localStorage.setItem('settings', 'Simpl');
            global.refreshCanvas();
        }

    }

    return (
        <>
            <Button className="bottom-button" variant="outlined" color={sText === "Advanced" ? 'secondary' : 'primary'} onClick={changeSettingsType}>Open {sText} Settings</Button>&nbsp;
            <Button className="bottom-button" variant="outlined" onClick={saveToClipboard}>{text}</Button>
        </>
    )
}

export default TopSelection;